.bg {  
  width: 100wh;
  height: 100vh;
  background-color: #EDCDC2;
}

.bgImage {
  position: absolute;
  bottom: 0;
  left: 0;
  max-width: 70vw;
  max-height: 70vh;
  object-fit: contain;
}

.balloon {
  position: fixed;
  width: auto;
  padding: 20px;
  box-sizing: border-box;
  text-align: center;
  min-width: 200px;
  max-width: 400px;
  word-wrap: break-word;
  color: #BFCAD6;
  background-color: #BE3455;
  border: solid 2px #BFCAD6;
  border-radius: 20px;
  backdrop-filter: blur(10px);

  animation-name: moveUp;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-fill-mode: both;
}

@keyframes moveUp {
  from {
    bottom: -200px;
  }
  to {
    bottom: 100vh;
  }
}